.about-container{
    display: grid;
    grid-template-columns: 1fr 1fr;
    background-color: white;
    color: black;
    height: 70vh;
    padding: 100px 0 100px 0;
}

.about-desc{
    margin-top: 20px;
    justify-content: center;
    align-items: center;
    width: 80%;
}

.about-desc > h3{
    margin-bottom: 30px;
    font-size: 28px;
    text-align: center;
}

.about-desc >p{
    line-height: 1.5;
    font-size: 18px;
    text-align: center;
}


.about-img > img{
    width: 80%;
    margin-left: 50px;
    box-shadow: 2px 2px 17px 9px rgba(0,0,0,0.75);
-webkit-box-shadow: 2px 2px 17px 9px rgba(0,0,0,0.75);
-moz-box-shadow: 2px 2px 17px 9px rgba(0,0,0,0.75);
border-radius: 10%;
}

 @media only screen and (max-width:1000px){
    .about-container{
        grid-template-columns: 1fr;
        padding: 0%;
        height: 70vh;
    }
    .about-desc{
        width: 90%;
        margin: 0 auto;
        padding: 40px 0 0 0;
        text-align: center;
    }
    .about-desc >h3{
        font-size: 200%;
        text-align: center;
    }

    .p2{
        padding-top: 10px;
        text-align: center;
    }
    .about-img{
        width: 80%;
        align-items: center;
    }
    .about-img >img{
        display: none;

    }
    .about-desc >p{
        font-size: 100%;
    }
 }
 @media only screen and (max-width:430px){
    .about-desc >p{
        font-size: 15px;
    }
 }