.navbarContainer{
    padding: .4rem;
    background-color: #000;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: fixed;
    width: 100%;
    z-index: 100;
    }
    
    .h2navBar{
        color: #fff;
        font-weight: 400;
    }
    span{
      font-weight: bold;
  }
    
    a{
        color: #fff;
        text-decoration: none;
        margin-right: 1rem;
    }

    .links-nav{
        border-left: 2px solid transparent;
        border-right: 2px solid transparent;
        padding-left: 5px;
        padding-right: 5px;
    }

    .links-nav:hover{
        border-color: white;
        text-shadow: 0 0 15px rgba(255,255,255,.5), 0 0 10px rgba(255,255,255,.5);
        transition: all 0.4s ease-in;
    }

    


    @media (min-width: 768px){
        .burger{
            display: none;
        }
        
    }

    @media(max-width:768px){
        .burger{
            position: fixed;
            margin-top: 50px;
            right: 0;

        }
       .h2navBar{
            display: none;
        }
        .links{
        position: absolute;
        right: 0%;
        top: -700px;
        left: -2000px;
        margin-left: auto;
        margin-right: auto;
        text-align: center;
        transition: all .5s ease;
        }
        a{
            color: #fff;
            font-size: 2rem;
            display: block;
            margin-top: 1rem;
        }
        .links.active{
            width: 100%;
            display: block;
            position: fixed;
            margin-left: auto;
            margin-right: auto;
            top: 30%;
            left: 0;
            right: 0;
            text-align: center;
        }
        .links-nav{
            border: none;
        }
        

    .bgDiv.active{
        top: 0%;
        left: 0%;
    }
    }

    .bgDiv{
        position: fixed;
        background-color: #000;
        top:-1000px;
        left: -1000px;
        width: 100%;
        height: 100%;
        z-index: -1;
        border-radius: 0 0 80% 0;
        transition: all .6s ease;
    }