.carousel-container{
    display: flex;
    vertical-align: center;
    align-items: center;
    justify-content: center;
    background-color: black;
    padding: 30px 0 30px 0;
    height: 85vh;

}

.carousel-title{
    color: white;
    width: 40%;
    text-align: center;
    font-size: 30px;
    padding: 0 100px;
}

.slide-container{
    height: 80%;
    border-radius: 40px;
    overflow: hidden;
    position: relative;
    cursor: pointer;
}


.slide-container:hover >img{
    transform: scale(1.2);
    opacity: .7;
}

.slide-desc{
    background-color: black;
    position: absolute;
    bottom: 0;
    right: 0%;
    width: 70%;
    padding-left: 30px;
    border-top-left-radius: 20px;
    opacity: .8;
}

.slide-desc > span{
    color: white;
    display: block;
    font-size: 22px;
    padding: 20px 0 20px 10px;
}

.slide-img{
    opacity: .7;
    width: 100%;
    height: 100%;
}

.img{
    height: 500px;

}
.slide-img:hover{
    opacity: 1;
    box-shadow: 2px 1px 31px 7px rgba(237,237,237,0.75);
-webkit-box-shadow: 2px 1px 31px 7px rgba(237,237,237,0.75);
-moz-box-shadow: 2px 1px 31px 7px rgba(237,237,237,0.75);
}

.BrainhubCarousel__arrows{
    border-radius: 50%;
    margin: 20px;
}

.BrainhubCarousel__arrows:hover{
    background-color: black !important;
}


@media only screen and (max-width: 900px){
    .carousel-container{
        flex-direction: column;

    }
    .carousel-title{
        width: 100%;
        text-align: center;
        padding-bottom: 90px;
        font-size: 5vw;
        position: relative;
    }
   
    .slide-desc{
        display: none;
    }
    .img{
        height: 50vh;
        width: 110%;
    }
}

