.footer{
    width: 100%;
    height: 100vh;
    background-color: #000;
    color: white;
    padding-top: 20vh;
    bottom: 0%;
    left: 0%;
}

.foot{
    display: grid;
    grid-template-columns: 3fr 1fr;
}

.footer-info{
    font-size: 35px;
    padding-left: 10vw;
}

.footer-contact{
    font-size: 22px;
    padding-top: 50px;
    padding-left: 10vw;

}

.sns-links{
    padding-top: 50px;
    padding-left: 9vw;
}

.iconos{
width: 60px;
  height: 60px;
  background-color: #fff;
  text-align: center;
  line-height: 57px;
  font-size: 35px;
  margin: 0 10px;
  display: inline-block;
  border-radius: 50%;
  position: relative;
  overflow: hidden;
  border: 3px solid #fff;
  z-index: 1;
}

.linkedin, .instagram, .whatsapp{
    position: relative;
  color: #000;
  transition: .5s;
  z-index: 3;
}


.iconos:hover .linkedin{
    color: #fff;
    transform: rotateY(360deg);
}
.iconos:hover .whatsapp{
    color: #fff;
    transform: rotateY(360deg);
}
.iconos:hover .instagram{
    color: #fff;
    transform: rotateY(360deg);
}

.iconos:before {
    content: "";
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    height: 100%;
    background: #f00;
    transition: .5s;
    z-index: 2;
  }

  .iconos:hover:before {
    top: 0;
  }

  .icono-w:before{
    background-color: #0dc143;
  }

  .icono-i:before{
    background: #f09433; 
    background: -moz-linear-gradient(45deg, #f09433 0%, #e6683c 25%, #dc2743 50%, #cc2366 75%, #bc1888 100%); 
    background: -webkit-linear-gradient(45deg, #f09433 0%,#e6683c 25%,#dc2743 50%,#cc2366 75%,#bc1888 100%); 
    background: linear-gradient(45deg, #f09433 0%,#e6683c 25%,#dc2743 50%,#cc2366 75%,#bc1888 100%); 
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f09433', endColorstr='#bc1888',GradientType=1 );
    ;
  }

  .icono-l:before{
    background-color: #0077b5;
  }


.form{
    display: flex;
    flex-direction: column;
    width: 50%;
    align-items: center;
    padding-right: 80px;
}

.name{
    padding: 5px;
    text-align: center;
    width: 350px;
}
.input-name, .input-email{
    padding-left: 5px;
    height: 35px;
    width: 350px;
    border-radius: 8px;
}

.email{
    padding: 5px;
    text-align: center;
    width: 350px;
}

.message{
    padding: 5px;
    text-align: center;
    width: 350px;
}

.join{
    padding-right: 100px;
    font-size: 20px;
    padding-top: 50%;
    text-align: center;
}

.input-text{
    height: 150px;
    width: 350px;
    border-radius: 8px;
    padding-left: 5px;
    padding-top: 5px;
}

.submit{
    margin-top: 5px;
    margin-bottom: 10px;
    width: 100px;
    height: 35px;
    font-size: 15px;
    cursor: pointer;
    border-radius: 10px;
    position: relative;
    overflow: hidden;
}

.submit:hover{
    background-color: #b361e2;
    color: #fff;
    transition: .5s;
}

@media only screen and (max-width:1110px){
    .footer{
        padding-top: 10vh;
        padding-left: 0;
    }
    .footer-info{
        font-size: 25px;
    }
    .footer-contact{
        font-size: 20px;
    }
}

@media only screen and (max-width:950px){
    .footer{
        padding-top: 15vh;
    }
    .footer-contact{
        display: none;
    }
    .join{
        padding-right: 0;
    }
    .izq{
        height: 20vh;
    }
    .footer-info{
        padding: 0%;
        text-align: center;
        top: 0;
        font-size: 110%;
    }

    .foot{
        flex-direction: column;
        display: flex;
        justify-content: center;
    }
    .der{
        display: flex;
        justify-content: center;
        padding-top: 20px;
    }
    .sns-links{
        display: flex;
        justify-content: center;
        padding: 0;
        padding-top: 90vh;
        background-color: #000;
    }
    .form{
        padding-right: 0;
    }
}

