.cover-container {
    height: 100vh;
    width: 100%;
    object-fit: contain;
    box-shadow:inset 0 0 0 1000pxx rgba(0, 0, 0, .6);
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    text-shadow: -2px 1px 6px rgba(0,0,0,0.6);
}

.coverVideo{
    object-fit: cover;
    width: 100%;
    height: 100vh;
    position: absolute;
    z-index: -2;
}

.title-container > h1{
    color: #fff;
    font-size: 100px;
}

.title-container > p{
    margin-top: 8px;
    color: #fff;
    font-size: 32px;
}

@media (max-width: 768px){
    .title-container  >h1{
        font-size: 50px;
    }
    .title-container >p{
        font-size: 20px;
    }
}