.container{
    height: 60vh;    
    padding-top: 25vh;
}

.nombre{
    text-align: center;
}

.h2-container >h2{
    color: #000;
    text-align: center;
    font-size: 35px;
    font-weight: bold;
}

.logos-container{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.logo{
    width: 100px;
}

.logo >img{
    width: 100%;    
    box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.75);
-webkit-box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.75);
-moz-box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.75);
border-radius: 10px;
}

@media only screen and (max-width: 900px){
   
.h2-container>h2{
    font-size: 200%
}
.nombre{
    font-size: 100%;
}
.logo{
    width:10vw;
}
}
@media only screen and (max-width: 600px){
    .nombre{
        display: none;
    }
}